.statistics {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    max-width: 480px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 40px;
    padding: 40px;
    background-color: #c1c8c7;
  }
  
  .title {
    margin: 0px;
    padding: 20px 0px;
    font-size: 24px;
    text-transform: uppercase;
    background-color: #f6fafb;
    color: #5e6668;
  }
  
  .statList {
    display: flex;
    flex-direction: row;
    padding: 0px;
    margin: 0px;
  }
  
  .item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    width: 80px;
    color: #f6fafb;
  }
  
  .percentage {
    font-size: 24px;
    padding-top: 10px;
  }